*{
  font-family: Arial, Helvetica, sans-serif;
}

body{
  padding: 60px;
} 

input{
  color: grey;
  font-size: 1.2rem;
  width: 100%;
}

.targeta{
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.targeta p{
  padding: 5px;
  text-align: center;
}

.targeta img{
  max-height: 150px;
}

.targeta-grid{
  display: flex;
  flex-direction: wrap;
  flex-wrap: wrap;
}